/* eslint-disable */
import { StaticImage } from "gatsby-plugin-image"
import React from "react"
import { useSelector, useDispatch } from "react-redux"
import { setFeedback } from "../redux/actions/button"
import { PHONE_NUMBER_MAIN } from "./consts"

function Feedback() {
  const feedback = useSelector(({ button }) => button.isReady)
  const dispatch = useDispatch()

  const TEL_REGEXP =
    "^(+7|7|8)?[s-]?(?[489][0-9]{2})?[s-]?[0-9]{3}[s-]?[0-9]{2}[s-]?[0-9]{2}$"

  const onCloseModal = () => {
    dispatch(setFeedback(false))
  }
  const onCloseModalLate = () =>
    setTimeout(() => {
      onCloseModal()
    }, 1000)
  return (
    feedback && (
      <div id="feedback" className="feedback">
        <div className="feedback__modal">
          <div className="feedback__modal__info">
            <h2> </h2>
            <p>
                        
              .
            </p>
            <p>      :</p>
            <div className="feedback__modal__info__phone">
              <StaticImage
                src="../images/icons/phone-min-white.png"
                alt="ag-phone-number"
              />
              <a href={`tel:${PHONE_NUMBER_MAIN}`}>{PHONE_NUMBER_MAIN}</a>
            </div>
          </div>
          <div className="feedback__modal__form">
            <button
              onClick={onCloseModal}
              className="feedback__modal__close__button"
            >
              <div
                style={{
                  transform: "rotate(45deg) translateY(1px)",
                }}
              ></div>
              <div
                style={{
                  transform: "rotate(-45deg) translateY(-1px)",
                }}
              ></div>
            </button>
            <form
              method="post"
              action="https://getform.io/f/d54e6c30-ad2c-4bcf-8463-3edecc795d82"
              target="_blank"
            >
              <input
                type="text"
                name="name"
                id="name"
                placeholder=""
                pattern="^[a-zA-Z--]+(([',. -][a-zA-Z-- ])?[a-zA-Z--]*)*$"
                required
              />
              <input
                type="email"
                name="email"
                id="email"
                placeholder="Email"
                pattern="^\w+([-+.']\w+)*@\w+([-.]\w+)*\.\w+([-.]\w+)*$"
              />
              <input
                type="tel"
                name="phone"
                id="tel"
                pattern="^(\+7|7|8)?[\s\-]?\(?[489][0-9]{2}\)?[\s\-]?[0-9]{3}[\s\-]?[0-9]{2}[\s\-]?[0-9]{2}$"
                placeholder="+7 (123) 456-78-90"
                required
              />
              <input
                type="message"
                name="message"
                id="message"
                placeholder=" ( )"
              />
              <input
                type="submit"
                name="submit"
                value=""
                style={{ cursor: "pointer" }}
                onClick={onCloseModalLate}
              />
            </form>
          </div>
        </div>
      </div>
    )
  )
}

export default Feedback
